.tutorial-card {
  opacity: 0;
  color: #fff;
  z-index: 999;
  pointer-events: auto;
  background: #000c;
  border-radius: 12px;
  width: 300px;
  padding: 1rem;
  font-family: sans-serif;
  transition: opacity 1s ease-out;
  position: absolute;
  top: 2rem;
  right: 2rem;
  box-shadow: 0 0 20px #00000080;
}

.tutorial-card.show {
  opacity: 1;
}

.card-header {
  border-bottom: 1px solid #555;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: .5rem;
  padding-bottom: .4rem;
  display: flex;
  position: relative;
}

.card-header h2 {
  margin: 0;
  padding-top: .2rem;
  font-size: 1.1rem;
}

.close-btn {
  cursor: pointer;
  color: #fff;
  font-size: 1.2rem;
  line-height: 1;
  transition: color .3s;
  position: absolute;
  top: 0;
  right: 0;
}

.close-btn:hover {
  color: #a9a9a9;
}

.tutorial-card ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.tutorial-card li {
  margin-bottom: .4rem;
  font-size: .95rem;
}
